import React from 'react';
import {
  Grid,
  GridItem,
  LabelGroup,
  Label,
} from '@patternfly/react-core';
import { billingModels } from '../../../../../common/subscriptionTypes';
import { humanizeValueWithUnitGiB } from '../../../../../common/units';
import {
  MACHINE_CIDR_PLACEHOLDER,
  SERVICE_CIDR_PLACEHOLDER,
  HOST_PREFIX_PLACEHOLDER,
  podCidrPlaceholder,
} from '../../CreateOSDForm/FormSections/NetworkingSection/networkingPlaceholders';
import parseUpdateSchedule from '../../../common/Upgrades/parseUpdateSchedule';

/**
 * reviewValues structure - key: field name
 * {
 *  title - human readable title
 *  values - map from values to human readable strings. optional.
 *           when both `values` and `valueTransform` are unspecified, actual value is shown.
 *  valueTransfrom - function to transform current value to human readable string,
 *                   gets two parameters: value (current value), allValues (all form values)
 *                   executed when `values` is not defined,
 *                   or when `values` has no entry for the provided value. optional.
 *  isBoolean - when set to `true`, value `undefined` will be treated as `false`,
 *             to match the behaviour of a boolean field.
 *  isMonospace - when set to `true`, value will be shown in monospace font.
 *  isOptional - when set to `true`, the field will only be shown when the value is not falsy.
 * }
 */
const reviewValues = {
  billing_model: {
    title: 'Subscription type',
    values: {
      [billingModels.STANDARD]: 'Annual: Fixed capacity subscription from Red Hat',
      [billingModels.MARKETPLACE]: 'On-Demand: Flexible usage billed through the Red Hat Marketplace',
      'standard-trial': 'Free trial (upgradeable)',
    },
  },
  byoc: {
    title: 'Infrastructure type',
    isBoolean: true,
    values: { // note: keys here are strings, on purpose, to match redux-form behaviour
      true: 'Customer cloud subscription',
      false: 'Red Hat cloud account',
    },
  },
  disable_scp_checks: {
    title: 'AWS service control policy (SCP) checks',
    valueTransform: value => (value ? 'Disabled' : 'Enabled'),
  },
  cloud_provider: {
    title: 'Cloud provider',
    valueTransform: value => value.toUpperCase(),
  },
  name: {
    title: 'Cluster name',
  },
  rosa_roles_provider_creation_mode: {
    title: 'Operator roles and OIDC provider mode',
  },
  custom_operator_roles_prefix: {
    title: 'Operator roles prefix',
  },
  cluster_version: {
    title: 'Version',
    valueTransform: value => value.raw_id,
  },
  region: {
    title: 'Region',
  },
  multi_az: {
    title: 'Availability',
    isBoolean: true,
    values: {
      true: 'Multi-zone',
      false: 'Single zone',
    },
  },
  persistent_storage: {
    title: 'Persistent storage',
    valueTransform: (value) => {
      const humanized = humanizeValueWithUnitGiB(parseFloat(value));
      return `${humanized.value} GiB`;
    },
  },
  load_balancers: {
    title: 'Load balancers',
  },
  enable_user_workload_monitoring: {
    title: 'User workload monitoring',
    isBoolean: true,
    values: {
      true: 'Enabled',
      false: 'Disabled',
    },
  },
  upgrade_policy: {
    title: 'Update strategy',
    valueTransform: value => (value === 'manual' ? 'Individual updates' : 'Recurring updates'),
  },
  automatic_upgrade_schedule: {
    title: 'Recurring update schedule',
    valueTransform: (value) => {
      const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
      const hours = [...Array(24).keys()].map(hour => `${hour.toString().padStart(2, 0)}:00`);
      const [hour, day] = parseUpdateSchedule(value);
      return `Every ${days[day]} at ${hours[hour]} UTC`;
    },
  },
  node_drain_grace_period: {
    title: 'Node draining',
    valueTransform: value => `${value} minutes`,
  },
  etcd_encryption: {
    title: 'Additional etcd encryption',
    isBoolean: true,
    values: {
      true: 'Enabled',
      false: 'Disabled',
    },
  },
  customer_managed_key: {
    title: 'Encrypt volumes with customer keys',
    isBoolean: true,
    values: {
      true: 'Enabled',
      false: 'Disabled',
    },
  },
  network_configuration_toggle: {
    title: 'Networking',
    values: {
      basic: 'Basic',
      advanced: 'Advanced',
    },
  },
  machine_type: {
    title: 'Node instance type',
  },
  autoscalingEnabled: {
    title: 'Autoscaling',
    isBoolean: true,
    values: {
      true: 'Enabled',
      false: 'Disabled',
    },
  },
  nodes_compute: {
    title: 'Compute node count',
    valueTransform: (value, allValues) => {
      if (allValues.multi_az === 'true') {
        return `${value / 3} (× 3 zones = ${value} compute nodes)`;
      }
      return value;
    },
  },
  min_replicas: {
    title: 'Compute node range',
    valueTransform: (value, allValues) => (
      <>
        <span>
          Minimum nodes
          {allValues.multi_az === 'true' ? ' per zone' : ''}
          :
        </span>
        {' '}
        {value}
        <span className="pf-u-ml-lg">
          Maximum nodes
          {allValues.multi_az === 'true' ? ' per zone' : ''}
          :
        </span>
        {' '}
        {allValues.max_replicas}
      </>
    ),
  },
  node_labels: {
    title: 'Node labels',
    valueTransform: labels => (
      <LabelGroup>
        {
            // eslint-disable-next-line react/destructuring-assignment
            labels.map(label => (
              <Label color="blue">
                {`${label.key} = ${label.value || ''}`}
              </Label>
            ))
          }
      </LabelGroup>
    ),
  },
  install_to_vpc: {
    title: 'Install into existing VPC',
    isBoolean: true,
    values: {
      true: 'Enabled',
      false: 'Disabled',
    },
  },
  use_privatelink: {
    title: 'PrivateLink',
    isBoolean: true,
    values: {
      true: 'Enabled',
      false: 'Disabled',
    },
  },
  aws_vpc: {
    title: 'VPC subnet settings',
    valueTransform: (value, allValues) => {
      let vpcs = [
        {
          az: allValues.az_0,
          privateSubnet: allValues.private_subnet_id_0,
          publicSubnet: allValues.use_privatelink ? undefined : allValues.public_subnet_id_0,
        },
      ];
      if (allValues.multi_az === 'true') {
        vpcs = [
          ...vpcs,
          {
            az: allValues.az_1,
            privateSubnet: allValues.private_subnet_id_1,
            publicSubnet: allValues.use_privatelink ? undefined : allValues.public_subnet_id_1,
          },
          {
            az: allValues.az_2,
            privateSubnet: allValues.private_subnet_id_2,
            publicSubnet: allValues.use_privatelink ? undefined : allValues.public_subnet_id_2,
          },
        ];
      }
      return (
        <Grid>
          <GridItem md={3}><strong>Availability zone</strong></GridItem>
          <GridItem md={3}><strong>Private subnet ID</strong></GridItem>
          {!allValues.use_privatelink
            ? <GridItem md={3}><strong>Public subnet ID</strong></GridItem>
            : null}
          <GridItem md={allValues.use_privatelink ? 6 : 3} />
          {vpcs.map(vpc => (
            <>
              <GridItem md={3}>{vpc.az}</GridItem>
              <GridItem md={3}>{vpc.privateSubnet}</GridItem>
              {!allValues.use_privatelink ? <GridItem md={3}>{vpc.publicSubnet}</GridItem> : null}
              <GridItem md={allValues.use_privatelink ? 6 : 3} />
            </>
          ))}
        </Grid>
      );
    },
  },
  gpc_vpc: {
    title: 'VPC subnet settings',
    valueTransform: (value, allValues) => (
      <Grid>
        <GridItem md={3}><strong>Existing VPC name</strong></GridItem>
        <GridItem md={3}><strong>Control plane subnet name</strong></GridItem>
        <GridItem md={3}><strong>Compute subnet name</strong></GridItem>
        <GridItem md={3} />
        <GridItem md={3}>{allValues.vpc_name}</GridItem>
        <GridItem md={3}>{allValues.control_plane_subnet}</GridItem>
        <GridItem md={3}>{allValues.compute_subnet}</GridItem>
        <GridItem md={3} />
      </Grid>
    ),
  },
  configure_proxy: {
    title: 'Cluster-wide proxy',
    isBoolean: true,
    values: {
      true: 'Enabled',
      false: 'Disabled',
    },
  },
  http_proxy_url: {
    title: 'HTTP proxy URL',
    isOptional: true,
  },
  https_proxy_url: {
    title: 'HTTPS proxy URL',
    isOptional: true,
  },
  additional_trust_bundle: {
    title: 'Additional trust bundle',
    isMonospace: true,
    isOptional: true,
  },
  network_machine_cidr: {
    title: 'Machine CIDR',
    valueTransform: (value) => {
      if (value) {
        return value;
      }
      return `${MACHINE_CIDR_PLACEHOLDER} (default)`;
    },
  },
  network_service_cidr: {
    title: 'Service CIDR',
    valueTransform: (value) => {
      if (value) {
        return value;
      }
      return `${SERVICE_CIDR_PLACEHOLDER} (default)`;
    },
  },
  network_pod_cidr: {
    title: 'Pod CIDR',
    valueTransform: (value, allValues) => {
      if (value) {
        return value;
      }
      return `${podCidrPlaceholder(allValues.cloud_provider)} (default)`;
    },
  },
  network_host_prefix: {
    title: 'Host prefix',
    valueTransform: (value) => {
      if (value) {
        return `/${value}`;
      }
      return `${HOST_PREFIX_PLACEHOLDER} (default)`;
    },
  },
  cluster_privacy: {
    title: 'Cluster privacy',
    values: {
      external: 'Public',
      internal: 'Private',
      undefined: 'Public',
    },
  },
  associated_aws_id: {
    title: 'AWS account ID',
  },
  installer_role_arn: {
    title: 'Installer role',
  },
  support_role_arn: {
    title: 'Support role ARN',
  },
  worker_role_arn: {
    title: 'Compute role',
  },
  control_plane_role_arn: {
    title: 'Control plane role',
  },
};

export default reviewValues;
